
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'

export default {
name: "applyWithdraw",
components: {
ViewBox,
XHeader,
Tab,
TabItem,
Cell,
Group,
XInput,
XButton,
Popup,
Swiper,
SwiperItem
},
created() {
this.queryUserBankCard();
this.queryWithdraw();
this.queryUser();
},
computed: {
handleCardNo: function () {
return '****' + this.withDrawObj.cardNo.substr(this.withDrawObj.cardNo.substr.length - 6);
}
},
data() {
return {
index: 0,
withDrawObj: {
quantity: '',
cardNo: '',
cardName: '',
cardId: ''
},
showCardPopup: false,
bankCardList: [],
withdrawList: [],
myIntegral: ''
};
},
methods: {
queryUserBankCard() {
this.$api
.queryUserBankCard({})
.then(data => {
if (data.code === 200) {
this.bankCardList = data.data.bankCards;
this.withDrawObj.cardNo = this.bankCardList[0].cardId;
this.withDrawObj.cardName = this.bankCardList[0].cardKind;
this.withDrawObj.cardId = this.bankCardList[0].id;
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
selectCard(item) {
this.withDrawObj.cardNo = item.cardId;
this.withDrawObj.cardName = item.cardKind;
this.withDrawObj.cardId = item.id;
this.showCardPopup = false;
},
applyWithdraw() {
let queryObj = {
cardId: this.withDrawObj.cardId,
quantity: this.withDrawObj.quantity
}
this.$api
.applyWithdraw(queryObj)
.then(data => {
if (data.code === 200) {
this.$vux.toast.text("申请提现已提交", "top");
this.queryUser();
this.index = 1;
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
queryWithdraw() {
this.$api
.queryWithdraw({
pageNo: 1,
pageSize: 10
})
.then(data => {
if (data.code === 200) {
this.withdrawList = data.data.items;
} else {
this.$vux.toast.text(data.message, "top");
}
});
},
queryUser() {
this.$api
.queryUser({})
.then(data => {
if (data.code === 200) {
this.myIntegral = data.data.user.withdrawAmount;
} else {
this.$vux.toast.text(data.message, "top");
}
});
}
}
};



